import { Component, Input } from '@angular/core';

import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
    selector: 'ngbd-modal-content',
    template: `

    <div class="modal-content gallery-content">
     <img class="thumbnail" src="{{selectedImage.path}}" >
       <img  class="arrow right activeArrow" (click)=navigate(false) src="data:image/svg+xml;base64,">
       <img _ngcontent-cpl-41="" class="arrow left activeArrow" (click)=navigate(true) src="data:image/svg+xml;base64,">
      <div  class=" btn-close-modal-gallery" (click)="activeModal.close('Close click')"><img _ngcontent-cpl-41="" class="action close" src="data:image/svg+xml;base64,"></div>
  `
})
export class NgbdModalContent {
    @Input() selectedImage;
    @Input() datasource;
    constructor(public activeModal: NgbActiveModal) {

    }
    navigate(forward) {
        var index = this.datasource.indexOf(this.selectedImage) + (forward ? 1 : -1);
        console.log(index);
        if (index >= 0 && index < this.datasource.length) {
            this.selectedImage = this.datasource[index];
        }
    }
}





