import { Component, Input } from '@angular/core';
import {NgbModal, NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {NgbdModalContent} from './gallery-modal.component';
@Component({
	selector: 'gallery',
	template: `
  	<ul id="thumbnailsList">
  	   <li *ngFor="let image of datasource" >
  	      <img src="{{image.path}}" class="tn"
  		  width="185" height="140"  
  		  data-toggle="modal" data-target="#selectedImageModal"
                  (click)="setSelectedImage(image); open()">
  	   </li>
  	</ul>
  `,
	styles: [`
  	ul { padding:0; margin:20px auto}
  	li { display:inline;}
        .tn{ 
	   margin:2px 0px;
	   box-shadow:rgba(153, 153, 153, 0.22) 1px 1px 3px 1px; 
	   cursor: pointer 
        }
  	
  `]
})
export class GalleryComponent {

	@Input() datasource;
	selectedImage;
	constructor(private modalService: NgbModal) { }
	setSelectedImage(image) {
		this.selectedImage = image;
	}
	open() {
		const modalRef = this.modalService.open(NgbdModalContent, { size:  "lg" , windowClass: 'dark-modal' });
		modalRef.componentInstance.selectedImage = this.selectedImage;
		modalRef.componentInstance.datasource = this.datasource;
	}
}


