import { NgModule } from '@angular/core';
import { ChartsModule } from 'ng2-charts/ng2-charts';
import { CommonModule } from '@angular/common';
import { DashboardComponent } from './dashboard.component';
import { DashboardRoutingModule } from './dashboard-routing.module';
import { HttpModule, JsonpModule } from '@angular/http';
import { AuthGuard } from './../_guards/index';
import { ItemComponent } from './item/item.component';
import { SimpleNotificationsModule } from 'angular2-notifications';
import { UserService, AuthenticationService } from './../_services/index';
import { ReadMoreComponent } from './../dashboard/read-more.component';
import { SharedModule } from './../shared/shared.module'
import { GalleryComponent } from './../dashboard/gallery.component';

import { CommentModule } from './../comments/comment.module';
import { EmitterService } from './emitter.service';
import { FormsModule } from '@angular/forms';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NgbdModalContent } from './gallery-modal.component'
@NgModule({
  imports: [
    CommonModule,
    SimpleNotificationsModule.forRoot(),
    DashboardRoutingModule,
    SharedModule,
    FormsModule,
    HttpModule,
    JsonpModule,
    CommentModule, 
    NgbModule.forRoot()

  ],
  declarations: [DashboardComponent, ReadMoreComponent,NgbdModalContent, GalleryComponent, ItemComponent],
   entryComponents: [NgbdModalContent],
  providers: [
    EmitterService,
    AuthGuard,
    AuthenticationService,
    UserService
  ]
})
export class DashboardModule { }



